let passFields = document.querySelectorAll(".pass-field")

passFields.forEach(el => {
    let field = el.querySelector("input")
    let toggle = el.querySelector(".pass-toggle")

    if(toggle){
        toggle.onclick = function () {
            if(toggle.classList.contains("_active")){
                toggle.classList.remove("_active")
                field.setAttribute("type","password")
            } else{
                toggle.classList.add("_active")
                field.setAttribute("type","text")
            }
        }
    }
})
